      justify-center
    >
      <v-flex xs12 sm12 md12>
        <h1 class="display-4 font-weight-bold mb-1">GoCheckIn</h1>
      </v-flex>
    </v-layout>
    <v-layout
      align-top
      row
      justify-center
    >

      <v-flex xs6 sm6 md6>
        <h1 class="heading">Scan Barcode to Login</h1>
        <v-dialog v-model="dialogQrcode" max-width="600px">
          <v-btn slot="activator" color="primary" fab large dark>
            <v-icon>blur_on</v-icon>
          </v-btn>

          <v-card>
            <v-card-title>
              <v-icon>blur_on</v-icon>
              <span class="headline">{{ formTitle }} </span>
              <v-icon>blur_on</v-icon>
              <!-- <h2 class="display-2 font-weight-thin mb-3">{{ formTitle }} </h2> -->
            </v-card-title>

            <v-card-text>
              <v-container grid-list-md>
                <v-layout wrap>
