<script>
import { QrcodeStream } from 'vue-qrcode-reader'
import { mapState, mapGetters, mapActions } from 'vuex'
import { CallDialog, Exceptions } from '@/utils'
import { components } from 'aws-amplify-vue'

export default {
  data() {
    return {
      dialogDialpad: false,
      dialogQrcode: false,
      qrPaused: false
    }
  },
  created () {
    this.qrPaused = false
  },
  computed: {
    formTitle () {
      return 'Scan your QR code to login!'
    },
    ...mapState({
      reservations: state => state.checkIn.reservations,
      reservation: state => state.checkIn.reservation,
    }),
  },
  methods: {
    async onDecode(decodedString) {
      
      let loginParams
      
      try {
        if (decodedString) {
          this.qrPaused = true
          loginParams = JSON.parse(decodedString)
        } else {
          this.dialogQrcode = false
          this.qrPaused = false
          return
        }        
      } catch (e) {
        window.alert('Please use correct barcode.')

        this.dialogQrcode = false
        this.qrPaused = false

        return this.$router.go()  
      }

      try {
        await this.customLogin(loginParams)
      } catch (e) {
        console.error(e)

        window.alert(e.message)

        this.dialogQrcode = false
        this.qrPaused = false

        return this.$router.go()        
      }

    },

    ...mapActions({
      customLogin: 'session/customLogin',
      logout: 'session/logout',
    })
  },
  components: {
    'qrcode-stream': QrcodeStream,
    'amplify-authenticator': components.Authenticator
  }
}
</script>

